@mixin position_center($pos: absolute) {
  position: $pos;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%); }

@mixin pos($position, $top:null, $right:null, $bottom:null, $left:null) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left; }

@mixin pseudo($content: "",$pos: absolute, $display: block) {
  content: $content;
  display: $display;
  position: $pos; }

@mixin circle($size) {
  width: $size;
  height: $size;
  border-radius: 50%; }
